<template>
	<div id="seniorAuthentication">
    <div class="header">
      <div class="van-hairline--bottom van-nav-bar">
        <div class="van-nav-bar__left">
          <i class="van-icon van-icon-arrow van-nav-bar__arrow" style="color: #000000 !important;" @click="$router.back(-1)"></i>
        </div>
        <div class="van-ellipsis van-nav-bar__title">高级认证</div>
      </div>
    </div>
    <div class="login_box">
      <div style="padding: 0 10px;border-radius: 5px;margin: 10px;" class="background_color_ffffff font_12">
        <div style="padding: 10px 0;border-bottom: 1px solid #eeeeee;">
          <img src="static/img/icon_xm_gjrz1.png" style="width: 18px;vertical-align: middle;"/>
          <span>姓名</span>
          <input placeholder="请输入真实姓名" class="firstAuthentication_input" v-model="relname"/>
        </div>
        <div style="padding: 10px 0;border-bottom: 1px solid #eeeeee;">
          <img src="static/img/icon_dh_gjrz1.png" style="width: 18px;vertical-align: middle;"/>
          <span>电话</span>
          <input placeholder="请输入真实电话" class="firstAuthentication_input" v-model="reltel"/>
        </div>
        <div style="padding: 10px 0;">
          <img src="static/img/icon_dz_gjrz1.png" style="width: 18px;vertical-align: middle;"/>
          <span>地址</span>
          <input placeholder="请输入可直接联系的地址" class="firstAuthentication_input" style="width: 68%;" v-model="address"/>
        </div>
      </div>
      <div style="padding: 10px;border-radius: 5px;margin: 10px;" class="background_color_ffffff font_12">
        <div style="width: 49%;display: inline-block" class="text_center">
          <img src="static/img/icon_sfz_gjrz1.png" style="width: 20px;vertical-align: middle;"/>
          <span class="font_12">身份证<span class="color_8b8b8b">(正面)</span></span>
          <br>
          <img :src="idcard" style="height: 80px;vertical-align: middle;margin-top: 5px;" @click="uploadImg('idcard')"/>
        </div>
        <div style="width: 49%;float: right;text-align: center;">
          <img src="static/img/icon_yyzz_gjrz1.png" style="width: 20px;vertical-align: middle;"/>
          <span class="font_12">营业执照</span>
          <br>
          <img :src="business" style="height: 80px;vertical-align: middle;margin-top: 5px;" @click="uploadImg('business')"/>
        </div>
      </div>
      <div style="margin-top: 30px;" class="text_center">
        <button class="login_button" @click="seniorAuthentication">认证</button>
      </div>
      <div class="text_center">
        <span class="font_12 color_c10804">审核需要时间,请耐心等待</span>
      </div>
    </div>
	</div>
</template>
<script>
	export default {
		name: 'seniorAuthentication',
		data() {
			return {
        relname:'',
        reltel:'',
        address:'',
        userId:localStorage.getItem('userId'),
        address:'',
        idcard:'static/img/zu1.png',
        business:'static/img/zu1.png',
			}
		},
		methods:{
      uploadImg(dataMap){
        let $this = this;
        dsBridge.call('uploadHeadImg');
        dsBridge.register('addHeadImgCallback', function(rpdata) {
          if(dataMap=='idcard'){
            $this.idcard = rpdata;
          }else{
            $this.business = rpdata;
          }
        })
      },
      seniorAuthentication(){
        let $this = this;
        let obj = {
          userid: $this.userId,
          relname: $this.relname,
          reltel: $this.reltel,
          address: $this.address,
          idcard:$this.idcard,
          business:$this.business,
        };
        let qs = require('qs');
        $this.$ajax.post("senior/addsenior", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.$toast({
              message: '高级认证提交成功,请耐心等待!',
              position: "bottom"
            });
            $this.$router.back(-1);
          }else{
            $this.$toast({
              message: rpdata.data.meta.msg,
              position: "bottom"
            });
          }
        })
        .catch(function(err) {})
      },
		}
	}
</script>
<style scoped>
  .login_button {
    width: 80%;
    height: 40px;
    line-height: 40px;
    border: 0;
    color: #ffffff;
    border-radius: 5px;
    background-color: #c10804;
  }
  .firstAuthentication_input {
    margin-left: 10px;
    border: 0;
    border-left: 1px solid #8b8b8b;
    padding-left: 10px;
  }
  .login_box{
    margin-top: 50px;
  }
</style>



